@use 'base/mixins' as m;
@use 'base/variables' as v;


.alerta {
  border-radius: 5px;
  margin: 0 auto;
  margin-top: 1rem;
  font-size: 1.4rem;
  padding: .8rem 0;
  text-align: center;
  font-weight: normal;
  text-transform: uppercase;
  font-family: v.$primaria;
  color: v.$blanco;

  @include m.tablet {
    width: 80%;
  }

}

.error {
  background-color: v.$rojo;
}

.correcto {
  background-color: v.$verde;
}