@use 'variables' as v;
@use 'mixins' as m;

html {
  font-size: 62.5%;
  box-sizing: border-box;
  position: relative;
  
}

body{

  background-color: lighten( v.$naranja, 27%);
  font-size: 1.6rem;

 
 
}
*, *:before, *:after {
  box-sizing: inherit;
}


.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}
a {
    text-decoration: none;
}

img {
  display: block;
    max-width: 100%;
    margin: 0 auto;
}

