p {
  font-family: "Nunito Sans", sans-serif;
  font-size: 1.6rem;
  font-weight: 700;
}

h1, h2, h3 {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

h1 {
  font-size: 3.5rem;
}

h2 {
  font-size: 2.7rem;
}

h3 {
  font-size: 2.2rem;
}

.contenido {
  margin-top: 10rem;
}
@media (min-width: 768px) {
  .contenido {
    display: grid;
    grid-template-columns: 2fr 1fr;
  }
}

.principal__logo {
  color: #454545;
  text-align: center;
}

.ingresar {
  background-color: #F0F0F0;
  width: 85%;
  margin: 0 auto;
  box-shadow: -7px 10px 25px -15px;
  border: 1px solid #FFEBB7;
}
@media (min-width: 768px) {
  .ingresar {
    width: 80%;
    margin-right: 0;
    margin-bottom: 1rem;
  }
}
.ingresar__textarea {
  text-align: justify;
  border: none;
  border-top: 35px solid transparent;
  border-image: url("../img/proxy.jpg") 50 fill;
  border-image-slice: 100;
  border-image-repeat: round;
  width: 100%;
  resize: none;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  background-color: #F0F0F0;
  color: #243763;
  font-size: 2rem;
  display: block;
  padding: 4rem 2rem;
  height: 50rem;
}
.ingresar__textarea:focus {
  outline: none;
}
@media (min-width: 768px) {
  .ingresar__textarea {
    height: 60rem;
    padding: 6rem;
  }
}
.ingresar__warning {
  user-select: none;
  margin: 0;
  font-size: 1.4rem;
}
.ingresar__actions {
  padding: 2rem;
}
.ingresar__btns {
  display: flex;
  flex-direction: column;
}
@media (min-width: 480px) {
  .ingresar__btns {
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    gap: 1rem;
  }
}
@media (min-width: 768px) {
  .ingresar__btns {
    gap: 2rem;
  }
}
.ingresar__btn {
  background-color: #243763;
  padding: 1rem 2rem;
  color: #F0F0F0;
  font-size: 1.5rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 0.7rem 0;
  border-radius: 1rem;
  border: 1px #243763 solid;
}
@media (min-width: 480px) {
  .ingresar__btn {
    font-size: 1.6rem;
  }
}
.ingresar__btn:hover {
  background-color: #16223e;
  cursor: pointer;
}
.ingresar__btn--blanco {
  background-color: #F0F0F0;
  padding: 1rem 2rem;
  color: #243763;
  font-size: 1.5rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 0.7rem 0;
  border-radius: 1rem;
  border: 1px #243763 solid;
}
@media (min-width: 480px) {
  .ingresar__btn--blanco {
    font-size: 1.6rem;
  }
}
.ingresar__btn--blanco:hover {
  background-color: #d7d7d7;
  cursor: pointer;
}

.mostrar {
  background-color: #FFEBB7;
  box-shadow: 0px -10px 9px -10px rgba(0, 0, 0, 0.75);
}
@media (min-width: 768px) {
  .mostrar {
    margin-top: 4rem;
    box-shadow: -8px 0px 9px -9px rgba(0, 0, 0, 0.75);
  }
}
@media (min-width: 768px) {
  .mostrar {
    position: relative;
    width: 100%;
  }
}
.mostrar__mensaje {
  padding: 2rem;
  text-align: center;
  color: #243763;
}
.mostrar__todo {
  padding: 3rem 1rem 1rem 1rem;
}
@media (min-width: 768px) {
  .mostrar__todo {
    padding: 20rem 3rem 2rem 2rem;
    height: 100%;
  }
}
.mostrar__img {
  display: none;
}
@media (min-width: 768px) {
  .mostrar__img {
    display: block;
  }
}
.mostrar__info {
  padding: 2rem;
}
.mostrar__recomendacion {
  font-size: 1.5rem;
  font-weight: 200;
}
.mostrar__text {
  display: none;
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
}
.mostrar__texto, .mostrar__text {
  width: 80%;
  margin: 0 auto;
  text-align: center;
  font-size: 1.8rem;
  color: #243763;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}
@media (min-width: 768px) {
  .mostrar__texto, .mostrar__text {
    text-align: left;
    margin: 0 auto;
    margin-bottom: 3rem;
  }
}

.footer {
  background-color: #454545;
  color: #F0F0F0;
  font-family: "Nunito Sans", sans-serif;
}
.footer__creditos {
  margin-top: 4rem;
  margin-bottom: 0;
  text-align: center;
  font-size: 1.6rem;
  padding: 1.4rem 0;
}

.alerta {
  border-radius: 5px;
  margin: 0 auto;
  margin-top: 1rem;
  font-size: 1.4rem;
  padding: 0.8rem 0;
  text-align: center;
  font-weight: normal;
  text-transform: uppercase;
  font-family: "Nunito Sans", sans-serif;
  color: #F0F0F0;
}
@media (min-width: 768px) {
  .alerta {
    width: 80%;
  }
}

.error {
  background-color: #a01f1f;
}

.correcto {
  background-color: #2c7e13;
}