
@use 'base/mixins' as m;
@use 'base/variables' as v;

.ingresar {
  background-color: v.$blanco;
  width: 85%;
  margin: 0 auto;
  box-shadow: -7px 10px 25px -15px;
  border: 1px solid v.$crema;

  @include m.tablet {
    width: 80%;
    margin-right: 0;
    margin-bottom: 1rem;
  }
  
  
  &__textarea {
    text-align: justify;
    border: none;
    border-top: 35px solid transparent; 
    border-image: url("../img/proxy.jpg") 50 fill;
    border-image-slice:100;
    border-image-repeat: round;
    width: 100%;
    resize:none;
    font-family: v.$secundaria;
    background-color: v.$blanco;
    color:v.$azul;
    font-size: 2rem;
    display: block;
    padding: 4rem 2rem;
    height: 50rem;

    &:focus {
      outline: none;
    }

    @include m.tablet {
      height: 60rem;
      padding: 6rem;
    }

    
  }
  &__warning {
    user-select: none;
    margin: 0;
    font-size: 1.4rem;
  }

  &__actions {
    padding: 2rem ;
  }

  &__btns {
    display: flex;
    flex-direction: column;


    @include m.phone {
      flex-direction: row;
      align-items: center;
      justify-content: space-evenly;
      gap: 1rem;
    }
   
    @include m.tablet {
      gap:2rem;
    }
  }

  &__btn {
    @include m.btn(v.$azul, v.$blanco, v.$azul);
    
  }
  &__btn--blanco {
    @include m.btn(v.$blanco, v.$azul, v.$azul);
   
  }
}