@use 'variables' as v;

/** Media Queries **/
@mixin phone{
    @media (min-width: v.$telefono) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
@mixin desktop{
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin tv{
    @media (min-width: v.$tv) {
        @content;
    }
}


@mixin btn($bgColor, $colorTexto, $border) {
    background-color: $bgColor;
    padding: 1rem 2rem;
    color: $colorTexto;
    font-size: 1.5rem;
    font-weight: 400;
    display: inline-block;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;
    display: block;
    width: 100%;
    margin: .7rem 0;
    border-radius: 1rem;
    border: 1px $border solid;

    @include phone {
        font-size: 1.6rem;
    }

   
    &:hover {
        background-color: darken( $bgColor, 10% );
        cursor: pointer;
    }
}

