@use 'base/mixins' as m;
@use 'base/variables' as v;






.mostrar {
  background-color: v.$crema;
  box-shadow: 0px -10px 9px -10px rgba(0,0,0,0.75);
  
  @include m.tablet {
    margin-top: 4rem;
    box-shadow: -8px 0px 9px -9px rgba(0,0,0,0.75);
  }
 

  @include m.tablet {
    position: relative;
     width: 100%;
  }

  &__mensaje {
    padding: 2rem;
    text-align: center;
    color: v.$azul;
    
  }

  &__todo {
    padding: 3rem 1rem 1rem 1rem;

   @include m.tablet {
    padding: 20rem  3rem 2rem 2rem;
    height: 100%;
   }
  }

  &__img {
    display: none;

    @include m.tablet {
      display: block;
    }
  }

  &__info {
    padding: 2rem;
  }

  &__recomendacion {
    font-size: 1.5rem;
    font-weight: 200;
  }
  &__text {
    display: none;
    height: 100%;
    flex-direction: column;
    justify-content:space-between;
    
  }

  &__texto, &__text {
    width: 80%;
    margin: 0 auto;
    text-align: center;
    font-size: 1.8rem;
    color: v.$azul;
    font-family: v.$secundaria;

    @include m.tablet {
      text-align: left;
      margin: 0 auto;
      margin-bottom: 3rem;
    }
  }


}